import React, { Component } from "react";
import Article from "../elements/Article";

class Articles extends Component {
  constructor(props) {
    super();
    this.state = { articles: [] };
  }

  componentDidMount() {
    let articles = [];

    articles.push(
      <div className="column" key={0}>
        <Article
          key={0}
          title="Self-Programming Artificial Intelligence Using Code-Generating Language Models"
          url="https://arxiv.org/abs/2205.00167"
          image="images/AlexPolo.jpg"
          heading="Preprint"
          extract="15th most popular machine learning paper in the world during the month of October 2022. We develop the first AI that uses computer language models to rewrite its own source code."
        />
      </div>
    )
    articles.push(
      <div className="column" key={0}>
        <Article
          key={0}
          title="Task Transfer and Domain Adaptation for Zero-Shot Question Answering"
          url="https://aclanthology.org/2022.deeplo-1.12/"
          image="images/AlexPolo.jpg"
          heading="NAACL"
          extract="We combine task transfer and domain adaptation to achieve zero-shot learning."
        />
      </div>
    )
    articles.push(
      <div className="column" key={0}>
        <Article
          key={0}
          title="Distributed Evolution Strategies Using TPUs for Meta-Learning"
          url="https://ieeexplore.ieee.org/document/9308334"
          image="images/AlexPolo.jpg"
          heading="IEEE"
          extract="We develop a data-efficient evolutionary algorithm for optimizing neural networks on distributed supercomputer clusters."
        />
      </div>
    )

    this.setState({ articles: articles });
  }

  render() {
    return (
      <section className="section" id="articles">
        <div className="container">
          <h1 className="title">Publications</h1>
          <div className="columns">{this.state.articles}</div>
        </div>
      </section>
    );
  }
}

export default Articles;
